.Content {
    display: table;
    width: 50%;
}
.NextStep{
    cursor: pointer;
}

.finalPage{
    text-align: center;
}
.finalPage h1{
    margin: 15px 0;
    font-size: 30px;
    font-weight: 800;
}

h1 {
    font-weight: 800;
    font-size: 50px;
}
h2 {
    font-size: 25px;
    padding-bottom: 20px;
}
h3, span {
    font-size: 30px;
}
p {
    padding-top: 15px;
    padding-bottom: 15px;
    border-radius: 10px;
    margin: 25px auto;
    font-weight: 600;
}
a.selectButton {
    text-decoration: none;
}
button.NextStep {
    height: 35px;
    border: 1px solid rgb(224, 91, 71);
    border-radius: 10px !important;
    background-color: #8F2F21;
    color: #FFF4EE !important;
    font-weight: 600;
    font-size: 17px;
    width: 125px;
    outline: none;

}

button.goBack {
    height: 35px;
    border-radius: 10px !important;
    border: 1px solid #FFF4EE;
    background-color: #FFF4EE;
    color: #DC725D !important;
    font-weight: 600;
    font-size: 15px;
    width: 100px;
    outline: none;
    margin-top: 25px;
}
.selectButton button {
    margin: 30px auto;
    display: block;
    background-color: #FFF4EE;
    border: #FFF4EE;
    border-radius: 10px;
    color: #DC725D !important;
    height: 80px;
    width: 200px;
    font-weight: 600;
    font-size: 35px;
}
button:disabled {
    border: 1px solid #BEA6A0;
    background-color: #BEA6A0;
}
