.Answers {
    list-style: none;
    margin: 0;
    padding: 0;
}
.Answers li {
    background-color: #F1ECDE;
    color: #BE5845;
    margin: 0 0 10px;
    border-radius: 10px;
    font-size: 18px;
    padding-bottom: 10px;
    padding-top: 10px;
    font-weight: 600;
}

.Answers li:hover {
    cursor: pointer;
}
.Answers .correct,
.Answers .correct:hover {
    background-color: #1db900;
    color: #fff;
}
.Answers .incorrect,
.Answers .incorrect:hover {
    background-color: #ec0000;
    color: #fff;
}
.Answers[disabled] {
    pointer-events: none;
    opacity: 0.7;
}